#components-layout-demo-custom-trigger .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}
#components-layout-demo-custom-trigger .trigger:hover {
  color: #1890ff;
}
#components-layout-demo-custom-trigger .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}
.portalmannagement-wrap .Content-main-wrap {
  overflow-y: auto;
}
.portalmannagement-wrap .ant-layout-header .icon {
  border-right: 1px solid #E0E0E0;
  display: inline-block;
  padding-right: 16px;
}
.portalmannagement-wrap .ant-menu.ant-menu-dark .ant-menu-item-selected,
.ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
  background-color: #3E78ED;
}
.portalmannagement-wrap {
  color: black;
}
.portalmannagement-wrap .ant-menu-dark,
.portalmannagement-wrap .ant-menu-dark .ant-menu-sub {
  color: #ffffff;
}
.portalmannagement-wrap .ant-menu-submenu-selected {
  color: #ffffff !important;
}
.portalmannagement-wrap .ant-layout-sider-collapsed .logo .pic {
  display: none;
}
.portalmannagement-wrap .ant-layout-sider-collapsed .logo .picb {
  display: block;
  width: 30px;
  height: auto;
}
.portalmannagement-wrap .span {
  padding-left: 28px;
  position: relative;
}
.portalmannagement-wrap .span::after {
  position: absolute;
  left: 13px;
  top: 50%;
  margin-top: -2px;
  width: 4px;
  height: 4px;
  background: #fff;
  border-radius: 50%;
  content: "";
}
.portalmannagement-wrap .ant-menu-light {
  background: #222636;
  color: #fff;
}
.portalmannagement-wrap .logo {
  background: #001529;
  color: #fff;
  border-bottom: 1px solid #000000;
  overflow: hidden;
}
.portalmannagement-wrap .logo .pic {
  height: auto;
  width: 180px;
}
.portalmannagement-wrap .logo .picb {
  display: none;
}
.portalmannagement-wrap .mannagement-touxiang {
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
.portalmannagement-wrap .mannagement-header {
  display: inline;
  width: 100px;
  height: 100%;
}
.portalmannagement-wrap .ant-layout-header {
  position: relative;
}
.portalmannagement-wrap .mannagement-header-right {
  position: absolute;
  text-align: right;
  right: 30px;
  display: flex;
  justify-content: space-around;
  top: 0;
  align-items: center;
}
.portalmannagement-wrap .mannagement-header-right .ant-btn-primary {
  margin-right: 0;
}
.portalmannagement-wrap .mannagement-header-right .ant-btn-primary span {
  margin: 0;
}
.portalmannagement-wrap .mannagement-header-right span {
  margin: 0 13px;
}
.DownOutlined-text {
  color: #ccc;
}
.portalmannagement-wrap .ant-collapse-header {
  background: #F7F9FB;
  border: none;
  color: #ccc;
}
.portalmannagement-wrap .Card-CheckCircleOutlined {
  margin-bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.portalmannagement-wrap .ant-collapse-content > .ant-collapse-content-box {
  padding: 0;
}
.portalmannagement-wrap .ant-card-body {
  line-height: 55px;
  padding: 0;
  margin-bottom: 0;
}
.addFaculty-con {
  width: 840px!important;
}
