.portalnews {
  width: 100%;
  height: 100%;
}
.portalnews .com-header {
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #EEEEEE;
}
.portalnews .com-header > div {
  height: 100%;
}
.portalnews .com-header .com-header-left {
  color: #000000;
}
.portalnews .com-header .com-header-left h3 {
  margin: 0;
  color: #000000;
  font-size: 18px;
  margin-left: 30px;
  font-weight: bold;
}
.portalnews .com-header .com-header-left .ant-btn {
  width: 90px;
  height: 32px;
  border: 1px solid #3E78ED;
}
.portalnews .com-header .com-header-left .ant-btn:nth-child(1) {
  background: #3E78ED;
  border-radius: 3px;
  color: #fff;
  margin-right: 50px;
  margin-top: 14px;
  float: right;
}
.portalnews .com-header .com-header-left .ant-btn:nth-child(2) {
  background: #FFFFFF;
  color: #3E78ED;
  border-radius: 3px;
}
.portalnews .com-header .com-header-right {
  float: right;
  margin-right: 30px;
}
.portalnews .com-header .com-header-right .ant-input-search {
  width: 220px;
  height: 30px;
  margin-right: 20px;
}
.portalnews .com-header .com-header-right .ant-input-search .ant-input {
  width: 100%;
  height: 100%;
  border-radius: 13.5px;
  background: #F4F4F4;
  color: #888888;
}
.portalnews .com-header .com-header-right .count {
  color: #888888;
}
.portalnews .table-content {
  margin-top: 20px;
  padding: 0px 30px;
  padding-bottom: 20px;
  position: relative;
}
.portalnews .table-content .mytable-wrapper {
  border: 1px solid #F3F3F3;
  border-top: none;
  height: calc(100% - 70px);
}
.portalnews .table-content .mytable-wrapper .ant-table-wrapper {
  height: 100%;
  min-height: 400px;
}
.portalnews .table-content .ant-table-thead {
  background: #F7F9FB;
  font-weight: 500;
  font-size: 14px;
  line-height: 120.7%;
  color: #000000;
}
.portalnews .table-content .ant-table-body tr,
.portalnews .table-content .ant-table-thead tr {
  height: 51px;
}
.portalnews .table-content .ant-table-body tr td,
.portalnews .table-content .ant-table-thead tr td {
  height: 51px;
  padding: 0px 16px;
  border-bottom: 1px solid #F3F3F3;
}
.portalnews .table-content .ant-table-body tr td:nth-child(2),
.portalnews .table-content .ant-table-thead tr td:nth-child(2),
.portalnews .table-content .ant-table-body tr th:nth-child(2),
.portalnews .table-content .ant-table-thead tr th:nth-child(2) {
  text-align: center ;
}
.portalnews .table-content .ant-table-body tr th,
.portalnews .table-content .ant-table-thead tr th {
  background: #F7F9FB !important;
  border-bottom: none;
}
.portalnews .table-content .ant-table-body tr th:first-child,
.portalnews .table-content .ant-table-thead tr th:first-child {
  width: 10%;
}
.portalnews .table-content .ant-table-body tr th:nth-child(2),
.portalnews .table-content .ant-table-thead tr th:nth-child(2) {
  width: 24%;
}
.portalnews .table-content .ant-table-body tr th:nth-child(3),
.portalnews .table-content .ant-table-thead tr th:nth-child(3) {
  width: 46%;
}
.portalnews .table-content .ant-table-body tr th:nth-child(4),
.portalnews .table-content .ant-table-thead tr th:nth-child(4) {
  width: 20%;
}
.portalnews .table-content .ant-table-body .ant-table-column-title,
.portalnews .table-content .ant-table-thead .ant-table-column-title {
  font-size: 14px;
  color: #000000;
  font-weight: bold;
}
.portalnews .table-content .ant-table-body .ant-table-row-cell-last,
.portalnews .table-content .ant-table-thead .ant-table-row-cell-last {
  text-align: right;
}
.portalnews .mytable-wrapper .ant-table-wrapper .handle {
  text-align: right;
  margin-right: 37px;
}
.portalnews .mytable-wrapper .ant-table-wrapper .handle span {
  display: inline-block;
  margin-right: 15px;
  font-size: 14px;
  color: #3E78ED;
  cursor: pointer;
}
.portalnews .mytable-wrapper {
  height: calc(100% - 60px);
}
.portalnews .mytable-wrapper .ant-table-wrapper {
  height: 100%;
  width: 100%;
}
.portalnews .mytable-wrapper .ant-table-wrapper .ant-table-thead tr th {
  text-align: center !important;
  background: #F7F9FB !important;
}
.portalnews .mytable-wrapper .ant-table-wrapper .ant-table-thead tr th .ant-table-column-title {
  font-size: 14px;
  color: #000000;
  font-weight: bold;
}
.portalnews .mytable-wrapper .ant-table-wrapper .ant-table-tbody tr td {
  text-align: center !important;
  border-bottom: 1px solid #F3F3F3;
}
.portalnews .mytable-wrapper .ant-table-wrapper .ant-table-tbody tr:nth-child(10n) td {
  border-bottom: none;
}
.portalnews .mytable-wrapper .ant-table-wrapper .key {
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #D5D8DE;
  border-radius: 4px;
  text-align: center;
  line-height: 30px;
  margin: 0 auto;
}
.portalnews .mytable-wrapper .ant-table-wrapper .text {
  padding: 20px 0;
  min-height: 70px;
  white-space: normal;
}
.portalnews .mytable-wrapper .ant-table-wrapper .table-img {
  width: 220px;
  height: 130px;
  border-radius: 6px;
}
.portalnews .mytable-wrapper .ant-table-wrapper .oper {
  display: flex;
  justify-content: center;
  padding-left: 6% !important;
}
.portalnews .mytable-wrapper .ant-table-wrapper .oper .anticon {
  color: #3E78ED;
  margin-right: 15px;
  font-size: 17px;
  cursor: pointer;
}
.portalnews .mytable-wrapper .ant-table-wrapper .oper img {
  margin-right: 20px;
  cursor: pointer;
}
.portalnews .paper-bottom {
  position: absolute;
  bottom: 20px;
  right: 47px;
}
.portalnews .table-top {
  padding-bottom: 20px;
  box-sizing: border-box;
  min-width: 447px;
}
.portalnews .table-top .table-top-item {
  margin-right: 30px;
  float: left;
}
.portalnews .table-top .table-top-item .ant-select {
  width: 180px;
  height: 30px;
}
.portalnews .Pagination-fy {
  display: block;
  padding: 20px 30px 20px 29px;
}
.portalnews .Pagination-fy .ant-select-selection__rendered {
  line-height: 30px!important;
  margin-right: 0px!important;
  padding: 0 11px!important;
  margin-left: 0!important;
}
.portalnews .Pagination-fy .ant-select-selection__rendered .ant-select-selection-selected-value {
  padding-right: 18px!important;
}
.removeModal .ant-modal-footer {
  border-top: none;
  justify-content: flex-end;
}
.removeModal .ant-modal-content {
  width: 60%;
  padding: 20px;
  right: -71px;
  top: 200px;
  padding-bottom: 0;
}
.addFaculty-con .row-warp {
  margin-bottom: 12px;
}
.addFaculty-con .row-warp .ant-col-4 {
  text-align: center;
}
.addFaculty-con .flex-top {
  text-align: center;
}
.addFaculty-con .pos-btn .ant-tag {
  margin-top: 6px;
}
