.reset-pass .ant-col-4 {
  text-align: right;
  margin-right: 20px;
  color: #2A283D;
  font-size: 14px;
}
.reset-pass .sneak {
  margin-bottom: 20px;
}
.reset-pass .sneak .ant-col-15 {
  color: #999999;
  font-size: 12px;
}
.Form-con .ant-col-9 span {
  display: block;
}
.Form-con .span-text span {
  line-height: 80px;
}
.Form-con .right_messagetext {
  float: right;
}
.Form-con .right_messagetext ul {
  display: flex;
  justify-content: space-between;
}
.Form-con .right_messagetext ul li {
  width: 80px;
  height: 80px;
  position: relative;
}
.Form-con .right_messagetext ul li .img {
  width: 80px;
  height: 80px;
  display: block;
}
.Form-con .right_messagetext ul li .img img {
  width: 100%;
  height: 100%;
}
.Form-con .right_messagetext ul li .text {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 20px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 20px;
  color: #FFFFFF;
  background: #3E78ED;
  content: "";
  margin-bottom: 0;
  text-align: center;
}
.Form-con .right_messagetext ul li .ul-icon {
  display: flex;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  justify-content: space-evenly;
  align-items: center;
}
.Form-con .right_messagetext ul li .ul-icon img {
  width: 15px;
  height: 15px;
  cursor: pointer;
}
